/* FONTS */
@import url("https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,400;0,700;1,400&display=swap");

/* COLOR PALETTE
https://www.canva.com/brand/kAEdcKfvj-0
****************************************/
:root {
  --primary: #050a30;
  --secondary: #bae8e8;
  --color3: #2c698d;
  --color4: #dcdcdc;
  --color5: #575757;
}
body {
  height: 100%;
  font-family: "Noto Serif";
  letter-spacing: -0.03em; /* makes space smaller */
  background-color: white;
}

/* Buttons
-------------------------------------------------*/
.btn,
.btn:focus,
.btn:active {
  background: white;
  color: var(--primary);
  border-radius: 5px;
  border-color: var(--primary);
  border: 1px solid;
  margin-right: 2px;
  margin-left: 2px;
  text-decoration: none;
  padding: 3px;
  display: inline-block;
}
.btn:hover {
  background: var(--secondary);
}

/* Images
-------------------------------------------------*/
img {
  width: 250px;
}

/* Content
-------------------------------------------------*/
.projects-container {
  width: 900px;
  margin: 10px auto 0;
  padding: 20px 0;
  display: block;
  text-align: center;
}
.project {
  align-content: center;
  text-align: center;
  width: 430px;
  height: 400px;
  display: inline-block;
  color: var(--secondary);
  margin: 2px 2px;
  padding: 0;
  border: 2px solid;
  border-radius: 10px;
}

/* Responsive styles
-------------------------------------------------*/
@media screen and (max-width: 1023px) {
  .project {
    display: block;
    width: 100%;
    height: 600px;
    border: none;
    border-radius: 0;
    border-bottom: 2px solid var(--primary);
  }
}
